.single-post header {
  text-align: left;

  .entry-title {
    text-align: center;
  }

  .meta {
    text-align: center;

    .label:nth-of-type(2) {
      display: none;
    }
  }
}

.grid-container {
  @extend .container;

  margin: 4em auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 1.5em;
  row-gap: 1.5em;
  max-width: 1100px;

  @media #{$medium-and-down} {
    display: block;
  }
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    display: flex;
    flex-wrap: wrap;
  }
}

.excerpt {
  border-radius: 0;
  box-shadow: $box-shadow;

  @media #{$medium-and-down} {
    margin: 2em 0;
  }
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    width: 29.5%;
    margin: 20px;
  }

  .entry-summary {
    padding: 0 20px 20px;
  }

  img {
    border-radius: 0;
    height: 150px;
    object-fit: cover;

    @media #{$medium-and-down} {
      height: 250px;
    }
  }

  .entry-title {
    margin: 10px 0;
  }

  .meta {
    margin: 10px 0;
  }
}



